.header {
    position: fixed;
    width: 100%;
    padding: 40px 0;
    transition: .8s linear;
    z-index: 100;
    &__inner {
        @include flex-b-c;
    }
    &__logo {
        @include flex-b-c;
        justify-content: center;
        svg {
            width: 64px;
            height: 53px;
            fill: #ffffff;
        }
        span {
            display: inline-block;
            margin-left: 10px;
            font-family: 'Raleway', san-serif;
            font-size: 28px;
            letter-spacing: -1px;
            color:#ffffff;
            text-transform: capitalize;
            font-weight: 400;
        }
    }
    &__links {
        display: flex;
        align-items: center;
        svg {
            margin-left: 25px;
            display: inline-block;
            width: 24px;
            height: 24px;
            fill: #ffffff;
            stroke: #ffffff;
            stroke-width: 0;
        }
    }
}
.menu {
    &__list {
        @include flex-b;
        padding-right: 15px;
        margin-left: auto;
        li {
            padding: 15px;
        }
        @media (max-width: 1220px) {
            display: block;
            position: fixed;
            top: 0;
            left: 0;
            right: 100%;
            width: 100%;
            height: 100%;
            padding-top: 15%;
            padding-left: 20px;
            background-color: #352f2d;
            z-index: -1;
            transition: all .4s linear;
        }
    }
    &__link {
        display: block;
        font-size: 14px;
        color: #ffffff;
        font-weight: 300;
        text-transform: capitalize;
        transition: all .3s linear;
        &:hover {
            color: $mainClr;
        }
    }
}
.header-sticky {
    background-color: $mainClr;
    box-shadow: 0 5px 19px 0 rgba(0,0,0,.07);
    padding: 15px 0;
}
@media (max-width: 1220px) {
    .header__links {
        margin-left: auto;
    }
    .menu__burger {
        position: relative;
        margin-left: 30px;
        display: inline-block;
        width: 28px;
        height: 20px;
        cursor: pointer;
        span {
            position: absolute;
            content: '';
            background: #ffffff;
            width: 100%;
            height: 2px;
            &:nth-child(1) {
                top: 0;
            }
            &:nth-child(2) {
                top: 50%;
                transform: translateY(-50%);
            }
            &:nth-child(3) {
                top: 93%;
            }
        }
    }
}
.menu__on {
    right: 0;
}
.menu__burger-active {
    transition: all .4s linear;
    span {
        transition: all .4s linear;
        &:nth-child(1) {
            transform: rotate(-45deg);
            top: 50%;
        }
        &:nth-child(3) {
            transform: rotate(45deg);
            top: 50%;
        }
        &:nth-child(2) {
            transition: all .2s linear;
            transform: translate(-50%, -50%);
            opacity: 0;
        }
    }
}