$mainClr: #988574;
$baseTxt: #8c8c8c;

$rale: "Raleway", sans-serif;

h1, 
h2,
h3,
h4,
h5,
h6 {
    font-family: $rale;
    font-weight: 500;
}

h1,
h2,
h3,
h4,
h5,
h6
	+p {
        margin-top: 0;
    }
h1 {
    font-size: 2.25rem;
    line-height: 120%;
    font-weight: 500;
    @media (max-width: 1024px) {
        font-size: 2rem;
    }
    @media (max-width: 768px) {
        font-size: 1.8rem;
    }
    @media (max-width: 580px) {
        font-size: 1.5rem;
    }
}
h2 {
    font-size: 1.875rem;
}
h3 {
    font-size: 1.75rem;
}

h4 {
    font-size: 1.375rem;
}
h5 {
    font-size: 1.25rem;
    font-weight: 500;
}
img {
    max-width: 100%;
}
section {
    padding: 80px 0px;
    @media (max-width: 1024px) {
       padding: 60px 0;
    }
    @media (max-width: 768px) {
        padding: 40px 0;
     }
}
.btn {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 53px;
    border-radius: 3px;
    padding: 0 40px;
    background-color: $mainClr;
    color: #ffffff;
    text-transform: uppercase;
    white-space: nowrap;
    font-size: 14px;
    line-height: 23px;
    font-weight: 700;
    font-family: "Raleway", sans-serif;
    box-shadow: inset 0 -5px 0  0 #726257;
    transition: all .3s ease;
    &:hover {
        background-color: #352f2d;

    }
    @media(max-width: 768px) {
        height: 48px;
        padding: 0 30px;
        font-size: 13px;
        line-height: 100%;
        font-weight: 600;
    }
}

.padding {
    padding: 0px 20%;
    @media (max-width: 1024px) {
        padding: 0 12%;
     }
     @media (max-width: 768px) {
        padding: 0 5%;
     }
     @media (max-width: 580px) {
        padding: 0;
     }
}


.swiper-button-next:focus,  
.swiper-button-prev:focus {
    outline: none;
}
