html {
	box-sizing: border-box;
	font-size: 16px;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

html,
body {
	height: 100%;
	position: relative;
}
button:focus,
input:focus {
	outline: transparent;
}

body {
	font-family: 'Montserrat', sans-serif;
	font-size: 16px;
	color: $baseTxt;
}
.container {
	max-width: 1200px;
	margin: 0px auto;
	padding: 0 15px;
}


ul {
	margin: 0;
	padding: 0;
}
li {
	list-style: none;
}

a {
	text-decoration: none;
}