
.main__block {
    background: url(/../img/main-bg.png) no-repeat;
    background-size: cover;
    background-position: center bottom ;
    height: 100vh;
    position: relative;
    .swiper-pagination {
        position: absolute;
        content: '';
        left: 50%;
        transform: translateX(-50%);
        bottom: 40px;
        width: 100%!important;
        text-align: center;
    }
    .swiper-pagination-bullet {
        width: 10px;
        height: 10px;
        margin: 5px;
        background-color: #8b8c8e;
        opacity: 1; 
        transform: scale(1);
    }
    .swiper-pagination-bullet-active {
        border: 2px solid $mainClr;
        background-color: transparent;
    }
    .swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-next-next{
        transform: scale(1);
    }
    .swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-next {
        transform: scale(1);
    }
}
.slider {
    &__main {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 100%;
        .swiper-button-next, 
        .swiper-button-prev {
            width: 40px;
            height: 73px;
            transition: transform .3s linear;
            @media (max-width: 1024px) {
                width: 32px;
                height: 68px;
            }
            @media (max-width: 768px) {
                
            }
            @media (max-width: 580px) {
                display: none;
            }
            &:focus,
            &:hover {
                transform: translateX(5px);
                border: 0;
            }
            svg {
               width: 100%;
               height: 100%;
               fill: $mainClr;
                
            }
            &:after {
                display: none;
            }
        }
        .swiper-button-disabled {
            opacity: 1;
           svg {
            fill: #8b8c8e;
           }
        }
    }
    &__item {
         text-align: center;
         color: #ffffff;
         padding: 0px 29%;
         @media (max-width: 1200px) {
            padding: 0 15%;
         }
         @media (max-width: 580px) {
             padding: 0 10px;
         }
         h1 {
             margin-bottom: 15px;
         }
         .main__title {
             font-size: 3.25rem;
             font-family: $rale;
             font-weight: 800;
             line-height: 100%;
             margin-bottom: 30px;
             @media (max-width: 1024px) {
                font-size: 3rem;
            }
            @media (max-width: 768px) {
                font-size: 2.5rem;
            }
            @media (max-width: 580px) {
                font-size: 2rem;
            }
         }
         p {
             line-height: 140%;
             font-weight: 300;
             margin-bottom: 50px;
            @media (max-width: 768px) {
                font-size: 0.85rem;
            }
         }
    }
   
   
}

.service {
    &__inner {
        @include flex-b;
        margin: -15px;
        flex-wrap: wrap;
    }
    &__block {
        padding: 15px;
        width: calc(100%/4);
        display: flex;
        justify-content: center;
         @media (max-width: 840px) {
            width: calc(100%/2);
        }
        @media (max-width: 480px) {
            width: 100%;
        }
    }
    &__item {
        text-align: center;
        position: relative;
        padding-bottom: 30px;
        &::after{
            position: absolute;
            content: '';
            left: 50%;
            transform: translateX(-50%);
            bottom: 0;
            width: 70px;
            height: 3px;
            background-color: #f1eee9;
        }
        transition: all .3s linear;
        svg {
            width: 44px;
            height: 44px;
            fill: #555555;
            margin-bottom: 30px;
        }
        h4 {
            text-transform: capitalize;
            font-weight: 600;
            color:  #555555;
            margin-bottom: 15px;
        }
        p {
            font-size: 14px;
            line-height: 24px;
            color: #8c8c8c;
            font-weight: 300;
        }
        &:hover {
            h4 {
                color: $mainClr;
            }
            svg {
                fill: $mainClr;
            }
            &::after {
                background-color: $mainClr;
            }
        }
    }
}

.idea {
    background-color: #fbf9f7;
    padding: 55px 0 0 0; 
    text-align: center;
    h2 {
        color: #555555;
        font-weight: 600;
        margin-bottom: 20px;
    }
    p {
        line-height: 28px;
        color: #8c8c8c;
        font-weight: 300;
    }
    &__images {
        position: relative;
        height: 480px;
        margin: 40px -30px 0;
        overflow: hidden;
        @media(max-width: 1230px) {
            margin: 40px 0 0 0;
        }
    }
    &__img {
        position: absolute;
        content: '';
        &.center {
            top: 0;
            left: 50%;
            transform: translateX(-50%);
            z-index: 1;
            width: 100%;
        }
        &.left {
            top: 80px;
            left: 0;
        }
        &.right {
            top: 80px;
            right: 0;
        }
    }
  
}
.purchase {
    background-image: linear-gradient(90deg, #211b19 0%, #4e3326 100%);
    padding: 25px 0;
    &__inner {
       @include flex-b-c;
       flex-wrap: wrap;

    }
    h3 {
        color: #ffffff;
        font-weight: 400;
        margin-bottom: 10px;
        span {
            color: $mainClr;
        }
    }
    h5 {
        color: #615855;
        font-weight: 300;
        margin:0 0 25px;
    }
    .btn {
        float: right;
    }
}

.products {
    padding-top: 50px;
    &__inner {
        text-align: center;
    }
    h2 {
        color: #555555;
        font-weight: 600;
        text-align: center;
        margin-bottom: 20px;
    }
    p {
        font-size: 16px;
        line-height: 28px;
        color: #8c8c8c;
        font-weight: 300;
        margin-bottom: 40px;
        text-align: center;
    }
    .nav {
        &__list {
            @include flex-b;
            flex-wrap: wrap;
            justify-content: center;
            margin-bottom: 25px;
            
        }
        &__link {
            display: inline-flex;
            justify-content: center;
            align-items: center;
            font-size: 14px;
            color: #8c8c8c;
            font-weight: 300;
            height: 32px;
            border-radius: 3px;
            padding: 0 20px;
            background-color: rgba(17,17,17,0);
            border: 1px solid #ebebeb;
            text-transform: capitalize;
            transition: all .3s linear;
            margin: 5px;
            &:hover,
            &.active {
                background-color: $mainClr;
                color: #ffffff;
            }
        }
    }
    &__list {
        @include flex-b;
        flex-wrap: wrap;
        margin: 0 -15px 25px -15px;
    }
    .btn {
        margin: 0 auto;
    }
    .product {
        &__block {
            display: block;
            width: calc(100% / 3);
            padding:15px;
            @media (max-width: 1024px) {
                width: calc(100%/2);
            }
            @media (max-width: 570px) {
                width: 100%;
            }
        }
        &__item {
            display: block;
            transition: .5s linear;
            &:hover {
                .product__content {
                    background-color: #352f2d;
                    transition: .3s ease-in;
                    h4 {
                        color: #ffffff;
                    }
                    p {
                        color: $mainClr;
                    }
                    &:after {
                        border-bottom: 15px solid #352f2d;
                    }
                }
            }
        }
        &__img {
            height: 240px;
            display: block;
            overflow: hidden;
            position: relative;
            transition: all .8s linear;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
            &:hover {
                
                .product__links {
                    transform: scale(1);
                }
            }
        }
        &__links {
            position: absolute;
            content: '';
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(0, 0, 0, .5);
            display: flex;
            justify-content: center;
            align-items: center;
            transform: scale(0);
            transition: transform .8s linear;
            svg {
                display: inline-block;
                margin: 12px;
                width: 25px;
                height: 25px;  
                fill: #ffffff;
                &:hover {
                    fill: $mainClr;
                }
            }
        }
        &__content {
            background-color: #fbf9f7;
            padding: 30px;
            position: relative;
            &:after {
                position: absolute;
                content: '';
                top: -15px;
                left: 20px;
                width: 0;
                height: 0;
                border-bottom: 15px solid #fbf9f7;
                border-left: 30px solid transparent;
                border-right: 30px solid transparent;
            }
            h4 {
                color: #c7b299;
                margin: 0 0 10px;
                text-align: left;
            }
            p {
                font-size: 14px;
                line-height: 20px;
                color: #d1d1d1;
                font-weight: 300;
                padding: 0;
                margin: 0;
                text-align: left;
            }
            
        }
    }
}
.video {
    .plyr__poster {
        background-size: cover;
    }
    .plyr__control--overlaid {
        width: 100px;
        height: 100px;
        border-radius: 50%;
        background-color: rgba(0, 0, 0, .4);
        &:after {
            position: absolute;
            content: '';
            top: 50%;
            left: 50%;
            width: 70px;
            height: 70px;
            transform: translate(-50%, -50%);
            background: url('../img/icon-play.svg') no-repeat/ cover;
        }
        &:hover {
            background-color: $mainClr;
        }
    }
    .plyr__control svg {
        display: none;
    }
    .plyr--video .plyr__controls {
        display: none;
    }
}

.features { 
    background-color: #fbf9f7;
    padding-bottom: 0;
    &__inner {   
        display: flex;
        h3 {
            margin-top: 17.5%;
            margin-bottom: 35px;
            color: #555555;
        }
        p {
            line-height: 28px;
            color: #8c8c8c;
            font-weight: 300;
        }
    }
    &__left-indent {
        position: relative;
        flex: 40%;
        // display: flex;
        // justify-content: center;
        // align-items: flex-end;
        img {
            position: absolute;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: left;
        }
    }
    &__right-indent {
        flex: 0 0 62.5%;
        padding-left: 15px;
    }
    &__list {
        margin: 40px 0 27.5% 50px;
        li {
            margin-bottom: 20px;
            color: #8c8c8c;
            font-weight: 300;
            position: relative;
            padding-left: 50px;
            &:after {
                position: absolute;
                content: '';
                top: 0;
                left: 0;
                width: 21px;
                height: 21px;
                border-radius: 10px;
                background-color: #c7b299;
            }
            &:before {
                position: absolute;
                content: '';
                border: solid #ffffff;
                border-width: 0 2px 2px 0;
                display: inline-block;
                padding: 3px;
                transform: rotate(-45deg);
                top: 6.5px;
                left: 6px;
                z-index: 10;

            }
        }
    }
}
.counters {
    background-image: linear-gradient(90deg, #211b19 0%, #4e3326 100%);
    &__inner {
        @include flex-b;
        justify-content: space-around;
        padding: 0 7%;
        margin: -15px -10px;
        @media(max-width: 820px) {
            padding: 0;
            justify-content: center;
        }
        @media(max-width: 820px) {
            flex-wrap: wrap;
        }  
    }
    &__column {
        width: calc(100%/5);
        padding: 15px 10px;
        display: flex;
        justify-content: center;

        @media(max-width: 780px) {
            width: calc(100%/3);
        }
        @media(max-width: 480px) {
            width: calc(100%/2);
        }
    }
    &__item {
        text-align: center;
        padding-bottom: 30px;
        position: relative;
        &:after {
            position: absolute;
            content: '';
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 30px;
            height: 2px;
            background-color: #ffffff;
        }
        svg {
            width: 44px;
            height: 44px;
            stroke: $mainClr;
            fill: $mainClr;
            margin-bottom: 28px;
            @media(max-width: 1024px) {
                width: 38px;
                height: 38px;
                margin-bottom: 20px;
            }
        }
    }
    &__number {
        font-size: 3rem;
        line-height: 80%;
        color: #ffffff;
        font-weight: 300;
        margin-bottom: 20px;
        @media(max-width: 1024px) {
            font-size: 2rem;
            margin-bottom: 15px;
        }
        @media(max-width: 780px) {
            font-size: 1.8rem;
            margin-bottom: 10px;
        }
    }
    &__text {
        font-size: 0.875rem;
        line-height: 140%;
        color: #c7b299;
        font-weight: 300;
        text-transform: uppercase;
    }

}
// Post 
.post {
    padding-bottom: 80px;
    .container {
        &> h2,
        &> p {
            text-align: center;
        }
        h2 {
            margin-bottom: 28px;
            @media (max-width: 1024px) {
                margin-bottom: 20px;
            }
            @media (max-width: 768px) {
                margin-bottom: 15px;
            }
        }
        p {
            line-height: 28px;
            font-weight: 300;
            margin-bottom: 40px;
        }
    }
    &__item {
        overflow: hidden;
        transition: transform .7s ease 0s;
        &:hover {
            .post__content {
                background-color: #352f2d;
                border-color: #352f2d;
                h5 {
                    color: $mainClr;
                }
                p {
                    color: #ffffff;
                }
            }
        }
    }
    &__item-img {
        height: 220px;
        position: relative;
        overflow: hidden;
        display: block;
        transition: transform .7s ease 0s;
        &:after {
            position: absolute;
            content: '';
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            background-color: rgba(0, 0, 0, .6);
            transform: scale(0);
            transition: transform .7s ease 0s;
        }
        &:hover {
            &:after {
                transform: scale(1);
            }
        }
        img {
            height: 100%;
            width: 100%;
            object-fit: cover;
        }
    }
    &__label {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        width: 50px;
        height: 60px;
        margin: 20px;
        border-radius: 3px;
        z-index: 10;
    }
    &__date {
        font-size: 24px;
        line-height: 100%;
        color: #ffffff;
        font-weight: 300;
        text-align: center;
        background-color: #363636;
        padding: 7px;
        border-top-left-radius: 3px;
        border-top-right-radius: 3px;
    }
    &__month {
        font-size: 12px;
        line-height: 100%;
        color: #ffffff;
        font-weight: 400;
        font-family: "Roboto", sans-serif;
        text-align: center;
        background-color: #242424;
        padding: 5px 10px 5px 10px;
        text-transform: capitalize;
        border-bottom-left-radius: 3px;
        border-bottom-right-radius: 3px;
    }
    &__content {
        padding: 30px;
        border-bottom: 1px solid #f7f7f7;
        border-left: 1px solid #f7f7f7;
        border-right: 1px solid #f7f7f7;
        h5 {
            margin: 0 0 15px;
            color: #555555;
        }
        p {
            font-size: 14px;
            line-height: 22px;
            color: #8c8c8c;
            font-weight: 300;
            margin-bottom: 20px;
        }
        @media (max-width: 580px) {
            padding: 15px;
        }
    }
    &__link {
        font-size: 14px;
        line-height: 20px;
        color: #c7b299;
        font-weight: 300;
        text-transform: capitalize;
        position: relative;
        display: inline-block;
        padding-right: 10px;
        &::after {
            position: absolute;
            content: '';
            top: 50%;
            transform: translateY(-50%);
            right: 0;
            widows: 0;
            height: 0;
            border-left: 5px solid #c7b199;
            border-top: 5px solid transparent;
            border-bottom: 5px solid transparent;
            
        }
    }
}
.js-post__slider {
    .slider__navs {
        @include flex-c-c;
    }
    .swiper-buttonNext,
    .swiper-buttonPrev {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        width: 25px;
        height: 25px;
        margin: 40px 5px 0 5px;
        border: 2px solid  rgba(0,0,0,.5);
        border-radius: 50%;
        svg {
            width: 5px;
            height: 10px;
            fill: rgba(0,0,0,1);
        }
        &:after {
            display: none;
        }
        &:hover {
            border-color: $mainClr;
            svg {
                fill: $mainClr;
            }
        }
    }
}
.partners {
    background: url('../img/partners-bg.png') no-repeat;
    background-position: 80% 57%;
    padding: 9% 0;
    position: relative;
    .swiper-wrapper {
        align-items: center;
        .swiper-slide {
            text-align: center;
        }
    }
    &:after {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, .7);
    }
    &__inner {
        position: relative;
        padding: 0 7%;
    }
    &__item {
        display: inline-block;
        text-align: center;
        img {
            width: 80%;
            margin: 0 auto;
        }
    }
   
}