.footer {
    &__top {
        background-color: #191919;
        padding: 7% 0;
    }
    &__top-inner {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin: 0 -15px  
    }
    &__logo {
        display: inline-flex;
        align-items: center;
        margin-bottom: 30px;
        svg {
            width: 65px;
            height: 55px;
            fill: #ffffff;
            display: inline-block;
            margin-right: 5px;
        }
        .logo__text {
            font-size: 34px;
            letter-spacing: -1px;
            color: #ffffff;
            font-weight: 400;
            font-family: $rale;
        }
    }
    &__column {
        width: calc(100%/4);
        padding: 0px 15px;
        p {
            font-size: 14px;
            line-height: 24px;
            color: #555555;
            font-weight: 300;
        }
        hr {
            border-bottom: 1px solid #242424;
        }
        @media (max-width: 1024px) {
            width: calc(100%/2);
            margin-bottom: 30px;
        }
        @media (max-width: 480px) {
            width: 100%;
            margin-bottom: 30px;
        }
    }
    &__title {
        font-size: 24px;
        color: #ffffff;
        font-weight: 700;
        font-family: $rale;
        margin-bottom: 28px;
        text-transform: capitalize;
    }
    &__date,
    &__time {
        font-size: 12px;
        line-height: 20px;
        color: #363636;
        font-weight: 300;
        margin-bottom: 10px;
        text-transform: capitalize;
    }
    &__widget-list {
        @include flex-b;
        flex-wrap: wrap;
        margin: 0 -5px;
        li {
            padding: 5px;
            width: calc(100%/2);
            height: 99px;
            overflow: hidden;
            display: block;
        }
    }
    &__widget-img {
        display: block;
        width: 100%;
        height: 100%;
        overflow: hidden;
       
        &::after {
            position: absolute;
            content: '';
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(0, 0, 0, .7);
            transform: scale(0)
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
            transition: all 0.5s ease 0s;
            
        }
        &:hover > img {
            transform: scale(1.6, 1.6) rotate(-20deg);
            opacity: 0.5;
          }
    }
    &__link {
        font-size: 14px;
        line-height: 20px;
        color: #d1d1d1;;
        font-weight: 300;
        text-transform: capitalize;
        position: relative;
        display: inline-block;
        padding-right: 10px;
        &::after {
            position: absolute;
            content: '';
            top: 50%;
            transform: translateY(-50%);
            right: 0;
            widows: 0;
            height: 0;
            border-left: 5px solid#d1d1d1;;
            border-top: 5px solid transparent;
            border-bottom: 5px solid transparent;
            
        }
    }
}